import React from 'react';
import '../styles/App.css'; // Ensure the path is correct

function UnsettledBets({ data }) {
  // Define the columns you want to display with their index positions from the data array
  const columnsToDisplay = [
    { index: 0, label: 'Date' }, // Date column index
    { index: 4, label: 'Stake Size' }, // Stake Size column index
    { index: 3, label: 'Sport' }, // Sport column index
    { index: 5, label: 'Match' }, // Match column index
    { index: 6, label: 'Selection' }, // Selection column index
    { index: 7, label: 'Odds' }, // Odds column index
    { index: 9, label: 'Bookie' }, // Bookie column index
    { index: 8, label: 'Result' }, // Result column index
    { index: 10, label: 'P/L' }, // P/L column index
    { index: 11, label: 'Rolling Profit' }, // Rolling Profit column index
    { index: 12, label: 'Rolling ROI' }, // Rolling ROI column index
    { index: 13, label: 'OC Link' }, // OC Link column index
  ];

  // Filter for unsettled bets (assuming an unsettled bet has no value in the Result column)
  const unsettledBets = data.slice(1).filter((bet) => !bet[8]); // Assuming index 8 is the Result column

  // Calculate the total number of unsettled bets
  const totalUnsettledBets = unsettledBets.length;

  // Calculate total stake of unsettled bets
  const totalUnsettledStake = unsettledBets.reduce((acc, bet) => acc + parseFloat(bet[4] || 0), 0); // Assuming index 4 is the Stake Size

  // Calculate number of bets and total stake by sport
  const betsBySport = unsettledBets.reduce((acc, bet) => {
    const sport = bet[3]; // Assuming sport is in column 3
    const stake = parseFloat(bet[4] || 0); // Assuming stake is in column 4

    if (!acc[sport]) {
      acc[sport] = { count: 0, stake: 0 };
    }

    acc[sport].count += 1;
    acc[sport].stake += stake;

    return acc;
  }, {});

  // Calculate total counts and stake across all sports
  const totalCountBySport = Object.values(betsBySport).reduce((acc, sportData) => acc + sportData.count, 0);
  const totalStakeBySport = Object.values(betsBySport).reduce((acc, sportData) => acc + sportData.stake, 0);

  return (
    <div className="unsettled-bets">
      <h2>Unsettled Bets</h2>

      {/* Summary Section */}
      <div className="unsettled-summary">
        <p><strong>Total Unsettled Bets:</strong> {totalUnsettledBets}</p>
        <p><strong>Total Unsettled Stake:</strong> {totalUnsettledStake.toFixed(2)}</p>

        <h3>Unsettled Bets by Sport</h3>
        <table className="summary-table">
          <thead>
            <tr>
              <th>Sport</th>
              <th>Number of Bets</th>
              <th>Total Stake</th>
            </tr>
          </thead>
          <tbody>
            {Object.entries(betsBySport).map(([sport, { count, stake }]) => (
              <tr key={sport}>
                <td>{sport}</td>
                <td>{count}</td>
                <td>{stake.toFixed(2)}</td>
              </tr>
            ))}
            {/* Summary row at the bottom */}
            <tr>
              <td><strong>Total</strong></td>
              <td><strong>{totalCountBySport}</strong></td>
              <td><strong>{totalStakeBySport.toFixed(2)}</strong></td>
            </tr>
          </tbody>
        </table>
      </div>

      {/* Unsettled Bets Table */}
      <table>
        <thead>
          <tr>
            {columnsToDisplay.map((column) => (
              <th key={column.index}>{column.label}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          {unsettledBets.length === 0 ? (
            <tr>
              <td colSpan={columnsToDisplay.length}>No unsettled bets</td> {/* Adjust colSpan based on displayed columns */}
            </tr>
          ) : (
            unsettledBets.map((bet, index) => (
              <tr key={index}>
                {columnsToDisplay.map(({ index: columnIndex }) => (
                  <td key={columnIndex}>
                    {columnIndex === 13 ? (
                      // Display as hyperlink if it is in the OC Link column
                      <a href={bet[columnIndex]} target="_blank" rel="noopener noreferrer">
                        {bet[columnIndex] ? 'OC Link' : 'No Link'}
                      </a>
                    ) : (
                      bet[columnIndex]
                    )}
                  </td>
                ))}
              </tr>
            ))
          )}
        </tbody>
      </table>
    </div>
  );
}

export default UnsettledBets;
