import React from 'react';
import '../styles/App.css'; // Ensure the path is correct

function AllBets({ data }) {
  // Define the columns you want to display with their index positions from the data array
  const columnsToDisplay = [
    { index: 0, label: 'Date' }, // Date column index
    { index: 4, label: 'Stake Size' }, // Stake Size column index
    { index: 3, label: 'Sport' }, // Sport column index
    { index: 5, label: 'Match' }, // Match column index
    { index: 6, label: 'Selection' }, // Selection column index
    { index: 7, label: 'Odds' }, // Odds column index
    { index: 9, label: 'Bookie' }, // Bookie column index
    { index: 8, label: 'Result' }, // Result column index
    { index: 10, label: 'P/L' }, // P/L column index
    { index: 11, label: 'Rolling Profit' }, // Rolling Profit column index
    { index: 12, label: 'Rolling ROI' }, // Rolling ROI column index
    { index: 13, label: 'OC Link' }, // OC Link column index
  ];

  return (
    <div className="all-bets">
      <h2>All Bets</h2>
      <table>
        <thead>
          <tr>
            {columnsToDisplay.map((column) => (
              <th key={column.index}>{column.label}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          {data.slice(1).map((bet, index) => {
            const result = bet[8]?.toUpperCase(); // Check the value of the "Result" column (index 8)
            let rowClassName = '';

            if (result === 'W') {
              rowClassName = 'win-row';
            } else if (result === 'L') {
              rowClassName = 'lose-row';
            } else if (result === 'V') {
              rowClassName = 'void-row';
            } else {
              rowClassName = 'neutral-row'; // Default class for other cases
            }

            return (
              <tr key={index} className={rowClassName}>
                {columnsToDisplay.map((column) => (
                  <td key={column.index}>{bet[column.index]}</td>
                ))}
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}

export default AllBets;
